.Portfolio {
    position: relative;
}

.Portfolio .box {
    border-radius: 10px;
    position: relative;
}

.Portfolio .box:hover {
    background: none;
    transform: translateY(-0);
}

.Portfolio .img {
    height: 300px;
    overflow: hidden;
    transition: 0.5s;
}

.Portfolio img {
    width: 100%;
    border-radius: 10px;
    transition: 0.5s;
}

.Portfolio .box .img:hover img {
    transform: scale(1.2);
}

.Portfolio .category {
    margin: 20px 0 30px 0;
    text-transform: uppercase;
}

.Portfolio .category span {
  color: #ffffff;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
}

.Portfolio .arrow {
  position: absolute;
  right: 30px;
  bottom: 11%;
  transform: rotate(-45deg);
  color: #CF0060;
  opacity: 0;
  transition: 0.5s;
}

.Portfolio .title:hover .arrow {
  opacity: 1;
}

.Portfolio h2 {
  font-size: 18px;
  transition: 0.5s;
  padding-bottom: 30px;
  color: #3c3e41;
}

.Portfolio .box:hover h2 {
  color: #CF0060;
}

.Portfolio .box:hover {
  color: black;
}

/*------------popup--------*/
body.active-modal {
  overflow-y: hidden;
}

.btn-modal {
  padding: 10px 20px;
  display: block;
  margin: 100px auto 0;
  font-size: 18px;
}

.modal,
.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
  z-index: 9999;
}

.overlay {
  background-color: #000117;
}

.modal-content {
  position: absolute;
  top: 12%;
  left: 8%;
  line-height: 1.4;
  background: #FFA300;
  padding: 14px 28px;
  border-radius: 3px;
  max-width: 85%;
  min-width: 60%;
  z-index: 9999;
  box-shadow: 4px 4px 8px #FFA300, -4px -4px 8px #CF0060;
  padding: 50px;
  border-radius: 10px;
}

.modal-text {
  padding: 0 100px 0 50px;
}

.modal-text h1 {
  margin: 20px 0 20px 0;
  font-size: 30px;
}

.modal-text p {
  font-size: 17px;
  margin-bottom: 20px;
  line-height: 25px;
}

.modal-text button {
  margin-right: 20px;
  color: #ffffff;
}

.modal-text button i {
  margin-left: 10px;
}

.modal-text button.close-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 50%;
  padding: 0 0;
  font-size: 20px;
}

.modal-text button.close-modal i {
  margin: 0;
}

/*------------popup--------*/
@media (max-width: 768px) {
    .modal,
    .overlay {
      overflow: scroll;
    }
  
    .modal .modal-img {
      margin: 50px 0 50px 0;
    }
    .modal-content {
      flex-direction: column;
    }
    .modal .right,
    .modal .left {
      padding: 0;
    }
  }